.quantity .minus::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  content: '';
  width: 8px;
  height: 2px;
  background-color: #6c6c6c
}

.quantity .plus {
  position: relative;
  border-left: 0px none;
  border-radius: 0 4px 4px 0
}

.quantity .plus::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  content: '';
  width: 8px;
  height: 2px;
  background-color: #6c6c6c
}

.quantity .plus::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  content: '';
  width: 2px;
  height: 8px;
  background-color: #6c6c6c
}

.quantity .minus.disabled::before {
  background-color: #dddddd
}

.quantity .plus.disabled::before {
